import * as React from 'react';
import Link from '@mui/material/Link';
import Typography from '@mui/material/Typography';
import Title from './Title';
import { Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';

function preventDefault(event) {
  event.preventDefault();
}

function createData(title, data) {
  return { title, data };
}

const rows = [
  createData('Loan Amount', "$500,000"),
  createData('Interest rate', "1.2%"),
  createData('Loan term', "30 years"),
  createData('Total Interest', "$20,000"),
];

export default function CreditProposal() {
  return (
    <React.Fragment>
      <Title>Estimated Payment</Title>

      <Typography component="p" variant="h4">
        $3,024.00
      </Typography>
      <Typography color="text.secondary" sx={{ flex: 1 }}>
        per month
      </Typography>
      <div>

        <TableContainer component={Paper}>
          <Table sx={{ minWidth: 250 }} size="small" aria-label="a dense table">

            <TableBody>
              {rows.map((row) => (
                <TableRow
                  key={row.title}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                >
                  <TableCell component="th" scope="row">
                    {row.title}
                  </TableCell>
                  <TableCell align="right">{row.data}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </div>
    </React.Fragment>
  );
}
