import { createSlice } from '@reduxjs/toolkit'

export const loanSlice = createSlice({
    name: 'loan',
    initialState: {
        value: {
            loanterms: 10,
        interestrate: 1.2
        }
    },
    reducers: {
        loan_term: (state, action) => {
            state.value = {
                interestrate: state.value.interestrate,
                loanterms: action.payload
                
            }
        },
        interest_rate: (state, action) => {
               state.value = {
                interestrate: action.payload,
                
                loanterms: state.value.interestrate
                
            }
        },
    }

})

export const { loan_term, interest_rate} = loanSlice.actions;

export  const selectLoanValue = state => state.value